import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VoucherComponent } from './voucher.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';



const routes: Routes = [
  {
    path: '',
    component: VoucherComponent
  },
  {
    path: 'list-voucher',
    loadChildren: () => import('./list-voucher/list-voucher.module').then(m => m.ListVoucherModule)
  },
  {
    path: 'releases-voucher',
    loadChildren: () => import('./releases-voucher/releases-voucher.module').then(m => m.ReleasesVoucherModule)
  },
  {
    path: 'buy-voucher',
    loadChildren: () => import('./buy-voucher/buy-voucher.module').then(m => m.BuyVoucherModule)
  },
  {
    path: 'report-voucher',
    loadChildren: () => import('./report-voucher/report-voucher.module').then(m => m.ReportVoucherModule)
  },
  {
    path: 'design',
    loadChildren: () => import('./design/design.module').then(m => m.DesignModule)
  },
  {
    path: 'exchange-points',
    loadChildren: () => import('./exchange-points/exchange-points.module').then(m => m.ExchangePointsModule)
  },
  {
    path: 'exchange-voucher-invoice',
    loadChildren: () => import('./exchange-voucher-invoice/exchange-voucher-invoice.module').then(m => m.ExchangeVoucherInvoiceModule)
  },
]

@NgModule({
  declarations: [VoucherComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class VoucherModule { }
